/* PendingServicesComponent.css */

.pending-services-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #202d3d;
    /* Use your desired bluish dark color here */
    color: #fff;
}

.pending-services-container h2 {
    margin-bottom: 20px;
}

.pending-services-container table {
    width: 100%;
    border-collapse: collapse;
}

.pending-services-container th,
.pending-services-container td {
    padding: 10px;
    text-align: center;
}

.pending-services-container th {
    background-color: #333;
    color: #fff;
}

.pending-services-container tr:nth-child(even) {
    background-color: #4a5568;
}

.pending-services-container tr:hover {
    background-color: #718096;
    cursor: pointer;
}